import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import "../styles/main-home.css"

export default function Home() {
  return (
    <Layout>
      <Seo title="ホーム"/>
      <div className="wrap">

        <section className="mv_hm"> 
          <div className="mv_hm-inner">
            <p className="txt1">
              DX時代の悩める事業者をお助けします！<br/>
              IT専門家×中小企業診断士が、<br/>
              あなたの会社のデジタル化＆IT活用を手厚くサポート。<br/>
              稼ぐ力を高め、新時代の成長に貢献します。
            </p>
            <p className="txt2">
              <span>小さな会社のためのデジタル戦略室</span><br/>
              株式会社ホットソウル
            </p>
          </div>
        </section>

        <section className="news">
          <div className="news-inner">
            <dl className="clearfix">
              <dt>2025.5.2</dt>
              <dd><Link to={`/case-study/ryugu-komiya`}>サポート実績「合同会社竜宮小宮 様」</Link>を公開</dd>
              <dt>2025.4.9</dt>
              <dd><Link to={`/column/about-our-corporate-philosophy`}>代表のコラム「企業理念について」</Link>を公開。<Link to={`/about-us`}>会社案内</Link>に企業理念の項目を追加</dd>
              <dt>2025.1.19</dt>
              <dd><Link to={`/`}>ホームページ</Link>を一部改装しました</dd>
              <dt>2024.10.2</dt>
              <dd><Link to={`/dx-council/november-22nd-security-seminar`}>情報セキュリティ勉強会＠藤沢 開催のお知らせ</Link>を公開</dd>
              <dt className="last">2024.7.17</dt>
              <dd><Link to={`/column/digitalization-beginners-workshop`}>代表のコラム「デジタル化はじめて勉強会」</Link>を公開</dd>
           </dl>
          </div>
        </section>

        <section className="kanagawa">
          <h2>提供サービス</h2>
          <div className="kanagawa-bg">
            <h3><span className="bg-yellow">デジタル化に関連する企業活動全般を、必要な期間・範囲で伴走支援いたします。</span></h3>
            <div className="kanagawa-inner">
              <div className="kanagawa-frame">
                <StaticImage 
                  className="" 
                  src="../images/main-home/scope_of_services.png"
                  alt="サービス提供の範囲"
                  placeholder="blurred"
                />
              </div>
            </div>
          </div>
        </section>
  
        <section className="kanagawa2">
          <div className="kanagawa2-inner">
            <StaticImage
              className="ph1"
              src="../images/main-home/kanagawa2-ph1.png"
              alt="“小さな会社のためのデジタル戦略室 ”株式会社ホットソウルが解決します！"
              placeholder="blurred"
            />
            <p className="txt1">
              <span className="bg-yellow">IT業界歴20年以上のエンジニア経験と、<br className="pc"/>中小企業診断士の知見を活かした、<br className="pc"/>経営とITのコンサルティングサービスです。</span>
            </p>
            <p className="txt2">
              「ITに詳しい社員がいない。でも、新しくIT担当者を雇用する余裕もない」<br className="pc"/>
                そんな経営者のお悩みを解決します。<br/>“あなたの会社のデジタル戦略室”として、お気軽にご相談ください。 
            </p>
          </div>
          <div className="main-home-btn">
              <Link to={`/contact-us`}>お問い合わせはこちら</Link>
          </div>
        </section>
  
        <section className="serviceEX">
          <h2>サービスメニュー</h2>
          <div className="serviceEX-inner">
            <div className="flex">
              <div className="flex-item">
                <p className="txt1"><span className="bg-yellow">デジタル化支援</span></p>
                <ul className="txt2">
                  <li className="txt3">・パソコンの導入、LAN構築</li>
                  <li className="txt3">・Excel、Accessのカスタマイズ</li>
                  <li className="txt3">・kintone、FileMaker導入</li>
                  <li className="txt3">・業務改善・効率化、生産性向上</li>
                  <li className="txt3">・ファイル管理・フォルダ構成見直し</li>
                  <li className="txt3">・ホームページ改修</li>
                  <li className="txt3">・ECサイト構築</li>
                  <li className="txt3">・webシステム開発、運用・保守</li>
                  <li className="txt3">・AWS環境構築</li>
                  <li className="txt3">・データベース及び、システムの統合</li>
                  <li className="txt3">・API開発</li>
                  <li className="txt3">・データ活用</li>
                  <li className="txt3">・AI活用</li>
                </ul>
              </div>
              <div className="flex-item">
                <p className="txt1"><span className="bg-yellow">情報セキュリティ・BCP</span></p>
                <ul className="txt2">
                  <li className="txt3">・管理体制構築と運用支援</li>
                  <li className="txt3">・事業継続力強化計画策定支援</li>
                  <li className="txt3">・社内教育</li>
                </ul>              
              </div>
              <div className="flex-item">
                <p className="txt1"><span className="bg-yellow">企業理念</span></p>
                <ul className="txt2">
                  <li className="txt3">・企業理念の明文化</li>
                  <li className="txt3">・社内浸透</li>
                </ul>                             
              </div>
              <div className="flex-item">
                <p className="txt1"><span className="bg-yellow">経営戦略・事業計画策定</span></p>
                <ul className="txt2">
                  <li className="txt3">・現状分析、課題抽出</li>
                  <li className="txt3">・アクションプラン策定</li>
                  <li className="txt3">・実行支援、モニタリング</li>
                  <li className="txt3">・各種補助金等の利用・申請支援</li>
                </ul>                                          
              </div>
              <div className="flex-item">
                <p className="txt1"><span className="bg-yellow">事業承継</span></p>
                <ul className="txt2">
                  <li className="txt3">・事業承継計画策定</li>
                  <li className="txt3">・実行支援</li>
                  <li className="txt3">・他士業連携</li>
                </ul>                  
              </div>
            </div>
            <div className="main-home-btn">
              <Link to={`/case-study/articles/`}>サポート実績をみる</Link>
            </div>
          </div>
        </section>
  
        <section className="serviceFeatures">
          <h2>サービスの特長</h2>
          <div className="serviceFeatures-inner">
            <div className="box">
              <h3><span className="bg-yellow">中小企業診断士&nbsp;×&nbsp;ITエンジニアのスキル融合</span></h3>
              <StaticImage
              className="ph1"
              src="../images/main-home/item4.png"
              alt="中小企業診断士×ITエンジニアのスキル融合"
              placeholder="blurred"
              />
              <div className="flex">
                <div className="flex-item left">
                  <ul className="txt2">
                    <li className="txt3">・専門用語を避けたやさしい言葉づかい</li>
                    <li className="txt3">・丁寧なヒアリングと説明</li>
                    <li className="txt3">・精緻な現状分析力、課題抽出力</li>
                    <li className="txt3">・チームを育てる盛り上げ力</li>
                  </ul>
                </div>
                <div className="flex-item right">
                  <ul className="txt2">
                    <li className="txt3">・システム開発の全工程を網羅する技術力</li>
                    <li className="txt3">・プログラムも書ける実装力</li>
                    <li className="txt3">・多様なベンダーをまとめる交渉力</li>
                    <li className="txt3">・豊富な業界ネットワーク</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="main-home-btn">
              <Link to={`/contact-us`}>お問い合わせはこちら</Link>
          </div>
        </section>

        <section className="serviceFlow">
          <h2>サービス提供プロセス</h2>
          <div className="serviceFlow-inner">
            <h3><span className="bg-yellow">優先課題に合わせた支援内容で、無駄な費用を抑えて伴走、貴社の成長に貢献致します。</span></h3>
            <StaticImage 
                  className="" 
                  src="../images/main-home/delivery_process.png"
                  alt="サービス提供プロセス"
                  placeholder="blurred"
            />
          </div>
          <hr/>
          <div className="serviceFlow-inner serviceFlow2">
            <h3>業務改善とクラウドツール導入の事例</h3>
            <StaticImage 
                  className="" 
                  src="../images/main-home/process_example.png"
                  alt="サービス提供プロセス（具体例）"
                  placeholder="blurred"
            />
          </div>
          <div className="main-home-btn3">
            <Link to={`/case-study/articles/`}>サポート実績をみる</Link>
          </div>
        </section>
      </div>
    </Layout>
  )
}
